<template>
  <div
    class="button"
    :id="comp.id"
    :style="comp.componentInfo.buttonStyle"
    :ref="comp.id"
  >
    <p :style="comp.componentInfo.textStyle">
      {{ comp.componentInfo.content }}
    </p>
  </div>
  <!-- </div> -->
</template>
<script>
export default {
  name: "button",
  props: {
    comp: {
      type: Object
    }
  },
  data() {
    return {
      wrapSize: null,
      scrollTop: 0,
      listener: null
    };
  },
  methods: {
    changeFloatPosition(val) {
      if (val) {
        window.lodash.forEach(this.positionStyle[val], (value, key) => {
          this.node.style[key] = value;
        });
      } else {
        this.node.style.position = "static";
        this.node.style.transform = "none";
        this.node.style["z-index"] = "1";
      }
    },
    getWrapSize() {
      let size = document.getElementById("viewport").getBoundingClientRect();
      return size;
    },
    changeScrollTop(val) {
      if (val !== "") {
        this.node.style.top = `${parseFloat(
          this.positionStyle[this.comp.componentInfo.position]["top"]
        ) + val}px`;
      }
    }
  },
  watch: {
    position(val) {
      this.changeFloatPosition(val);
    },
    scrollTop(val) {
      this.changeScrollTop(val);
    }
  },
  computed: {
    position() {
      return this.comp.componentInfo.position;
    },
    node() {
      return this.$refs[this.comp.id];
    },
    positionStyle() {
      return {
        tl: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.12}px`,
          top: `${this.wrapSize.height * 0.08}px`,
          transform: "none",
          "z-index": "999"
        },
        tc: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.5}px`,
          top: `${this.wrapSize.height * 0.08}px`,
          transform: "translateX(-50%)",
          "z-index": "999"
        },
        tr: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.67}px`,
          top: `${this.wrapSize.height * 0.08}px`,
          transform: "none",
          "z-index": "999"
        },
        bl: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.12}px`,
          top: `${this.wrapSize.height * 0.87}px`,
          transform: "none",
          "z-index": "999"
        },
        bc: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.5}px`,
          top: `${this.wrapSize.height * 0.87}px`,
          transform: "translateX(-50%)",
          "z-index": "999"
        },
        br: {
          position: "absolute",
          left: `${this.wrapSize.width * 0.67}px`,
          top: `${this.wrapSize.height * 0.87}px`,
          transform: "none",
          "z-index": "999"
        }
      };
    }
  },
  created() {
    let size = this.getWrapSize();
    this.wrapSize = {
      width: size.width,
      height: size.height
    };
    document.getElementById("viewport").addEventListener("scroll", e => {
      this.scrollTop = e.target.scrollTop;
    });
  },
  mounted() {
    this.changeFloatPosition(this.position);
  }
};
</script>
<style lang="stylus" scoped>
div
  cursor default
  .button
    display flex
    justify-content center
    align-items center
    background-color rgb(40,122,238);
    color white
</style>
